import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useParams } from "react-router-dom";

export const QueueComponent = ({
  getOwnerQueues,
  queues,
  isLoading,
  callNotice,
  finishNotice,
}) => {
  const [box, setBox] = useState("");
  useEffect(() => {
    getOwnerQueues();
  }, [getOwnerQueues]);
  const { id } = useParams();
  if (isLoading) {
    return <p>Loading...</p>;
  }
  if (!queues || !queues[id]) {
    return <p>No existe esta fila</p>;
  }
  console.log(queues[id]);
  return (
    <div>
      <h1>{queues[id].name}</h1>
      {queues[id].hasBoxes && (
        <input
          className="input"
          value={box}
          onChange={({ target }) => setBox(target.value)}
          placeholder="box"
        />
      )}
      <table class="table">
        <thead>
          <tr>
            <th>Orden</th>
            <th>Usuario</th>
            <th>Estado</th>
            <th>Acciones</th>
          </tr>
        </thead>
        <tbody>
          {queues[id]?.notices &&
            Object.values(queues[id].notices).map(
              ({ client, status, id: currentId, withoutApp }) => {
                if (status === "confirmed") {
                  return null;
                }
                return (
                  <tr key={currentId}>
                    <td>{currentId}</td>
                    <td>{client.username}</td>
                    <td>
                      {withoutApp ? (
                        "No tiene app"
                      ) : status === "waiting" ? (
                        <button
                          className="button"
                          onClick={() => {
                            if (queues[id].hasBoxes && !box) {
                              return alert("debes indicar en que box estas");
                            }
                            callNotice({ id: currentId, box });
                          }}
                        >
                          llamar
                        </button>
                      ) : (
                        <span>esperando</span>
                      )}
                    </td>

                    <td>
                      <button
                        onClick={() => {
                          finishNotice({ id: currentId });
                        }}
                      >
                        eliminar
                      </button>
                    </td>
                  </tr>
                );
              }
            )}
        </tbody>
      </table>

      {queues[id].hasRoom && <Link to={`/queues/${id}/room`}>adentro</Link>}
      <Link to={`/add-notice?queueId=${id}&isOwner=true`}>
        <button className="button">Agregar persona sin app</button>
      </Link>
      <Link to={`/queues/${id}/qr-code`}>
        <button className="button">Ver codigo QR</button>
      </Link>
    </div>
  );
};
