import { useEffect, useState } from "react";
import { Link, useHistory, useParams } from "react-router-dom";
import { QrCode } from "../../components/QrCode";

export const EditQueueComponent = ({
  getOwnerQueues,
  queues,
  isLoading,
  editQueue,
}) => {
  const [modifiedQueue, setModifiedQueue] = useState();
  const { id } = useParams();
  const history = useHistory();

  useEffect(() => {
    getOwnerQueues();
  }, [getOwnerQueues]);

  useEffect(() => {
    if (queues[id]) {
      setModifiedQueue(queues[id]);
    }
  }, [id, queues]);

  if (isLoading) {
    return <p>Loading...</p>;
  }
  if (!modifiedQueue) {
    return <div />;
  }

  return (
    <div style={{ display: "flex", flexDirection: "column" }}>
      <input
        value={modifiedQueue.name}
        onChange={({ target }) =>
          setModifiedQueue({ ...modifiedQueue, name: target.value })
        }
      />
      <label>
        has room
        <input
          type="checkbox"
          checked={!!modifiedQueue.hasRoom}
          onChange={() =>
            setModifiedQueue({
              ...modifiedQueue,
              hasRoom: !modifiedQueue.hasRoom,
            })
          }
          style={{
            backgroundColor: "initial",
            cursor: "default",
            appearance: "auto",
            boxSizing: "border-box",
            margin: "3px 3px 3px 4px",
            padding: "initial",
            border: "initial",
          }}
        />
      </label>
      <label>
        has boxes
        <input
          type="checkbox"
          checked={!!modifiedQueue.hasBoxes}
          onChange={() =>
            setModifiedQueue({
              ...modifiedQueue,
              hasBoxes: !modifiedQueue.hasBoxes,
            })
          }
          style={{
            backgroundColor: "initial",
            cursor: "default",
            appearance: "auto",
            boxSizing: "border-box",
            margin: "3px 3px 3px 4px",
            padding: "initial",
            border: "initial",
          }}
        />
      </label>

      {modifiedQueue.qrCode && (
        <div>
          <p>qr code:</p>
          <QrCode qrCode={modifiedQueue.qrCode} />
          <button
            onClick={() => {
              editQueue({ ...modifiedQueue, qrCode: null }, history.goBack);
            }}
          >
            Borrar qr
          </button>
        </div>
      )}
      <Link to={`/queues/${id}/add-qr-code`}>set qr code</Link>
      <button
        onClick={() => {
          editQueue(modifiedQueue, history.goBack);
        }}
      >
        Guardar
      </button>
    </div>
  );
};
