import { Formik, Form } from "formik";
import { TextField } from "../../components/TextField";
import * as Yup from "yup";

export const RegisterComponent = () => {
  const validate = Yup.object({
    email: Yup.string().email("el email es incorrecto").required("obligatorio"),
    password: Yup.string()
      .min(8, "minimo 8 caracteres")
      .max(16, "limite 16 caracteres")
      .required("obligatorio"),
    confirmPassword: Yup.string()
      .oneOf([Yup.ref("password"), null], "las contraseas deben coincidir")
      .required("obligatorio"),
  });
  return (
    <Formik
      initialValues={{
        firstName: "",
        lastName: "",
        email: "",
        password: "",
        confirmPassword: "",
      }}
      validationSchema={validate}
      onSubmit={(values) => {
        console.log(values);
      }}
    >
      {(formik) => (
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            width: "100%",
            alignItems: "center",
          }}
        >
          <h1 className="my-4-font-weight-bold-display-4">registrate</h1>
          <Form>
            <TextField label="email" name="email" type="text" />
            <TextField label="password" name="password" type="password" />
            <TextField
              label="confirm Password"
              name="confirmPassword"
              type="password"
            />
            <button className="btn btn-dark mt-3" type="submit">
              registrarse
            </button>
            <button className="btn btn-danger mt-3 ml-3" type="submit">
              volver a iniciar
            </button>
          </Form>
        </div>
      )}
    </Formik>
  );
};
