import { apiInstance } from "./apiInstance";

const QUEUES = "/queues";

export const getQueueApi = async ({ id }) => {
  return await apiInstance.get(`${QUEUES}/${id}`);
};

export const addQueueApi = async (queue) => {
  return await apiInstance.post(`${QUEUES}`, queue);
};

export const removeQueueApi = async ({ id }) => {
  return await apiInstance.delete(`${QUEUES}/${id}`);
};

export const editQueueApi = async ({ id, notices, ...queue }) => {
  return await apiInstance.patch(`${QUEUES}/${id}`, queue);
};

export const getOwnerQueuesApi = async ({ id }) => {
  return await apiInstance.get(`${QUEUES}?ownerId=${id}`);
};

export const getQrCodeQueueApi = async ({ qrCode }) => {
  return await apiInstance.get(`${QUEUES}?qrCode=${qrCode}`);
};
