import { connect } from "react-redux";
import { createNoticeWithQrAction } from "../../redux/actions/noticeActions";
import { JoinQueueComponent } from "./JoinQueueComponent";

const mapStateToProps = (state) => {
  return {
    queues: state.queue.queues,
    isLoading: state.global.isLoading,
    user: state.user.user,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    createNoticeWithQr: (qrCode, onSuccess) =>
      dispatch(createNoticeWithQrAction(qrCode, onSuccess)),
  };
};

export const JoinQueue = connect(
  mapStateToProps,
  mapDispatchToProps
)(JoinQueueComponent);
