import { connect } from "react-redux";
import { AddNoticeComponent } from "./AddNoticeComponent";
import { addNoticeAction } from "../../redux/actions";

const mapStateToProps = (state) => {
  return { user: state.user.user };
};

const mapDispatchToProps = (dispatch) => {
  return {
    addNotice: (notice, onSuccess) =>
      dispatch(addNoticeAction(notice, onSuccess)),
  };
};

export const AddNotice = connect(
  mapStateToProps,
  mapDispatchToProps
)(AddNoticeComponent);
