import { globalTypes } from "../types";

const {
  SET_ERROR_MESSAGE,
  CLEAR_ERROR_MESSAGE,
  SET_IS_LOADING,
  UNSET_IS_LOADING,
} = globalTypes;

const initialState = { errorMessage: "", isLoading: false };

export const globalReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_ERROR_MESSAGE:
      return {
        ...state,
        errorMessage: action.payload,
      };
    case CLEAR_ERROR_MESSAGE:
      return {
        ...state,
        errorMessage: "",
      };
    case SET_IS_LOADING:
      return {
        ...state,
        isLoading: true,
      };
    case UNSET_IS_LOADING:
      return {
        ...state,
        isLoading: false,
      };
    default:
      return state;
  }
};
