import React from "react";
import { ErrorMessage, useField } from "formik";

export const TextField = ({ label, ...props }) => {
  const [field, meta] = useField(props);
  return (
    <div className="mb-2">
      <input
        placeholder={label}
        {...field}
        {...props}
        error={meta.touched && meta.error}
        helperText={meta.touched && meta.error}
        style={{
          width: "250px",
          height: "50px",
          backgroundColor: "#E8E8E8",
          borderRadius: "20px",
          fontWeight: 600,
          fontSize: "22px",
          lineHeight: "28px",
          display: "flex",
          alignItems: "center",
          textAlign: "center",
          fontFamily: "Quicksand",
          borderColor: meta.touched && meta.error ? "#ff8d8d" : "#E8E8E8",
          borderWidth: "2px",
          borderStyle: "solid",
        }}
      />
      <p
        style={{
          color: "#ff8d8d",
          height: "20px",
          margin: 0,
          textAlign: "center",
        }}
      >
        {(meta.touched && meta.error) || "-"}
      </p>
    </div>
  );
};
