import { queueTypes } from "../types";

const {
  ADD_QUEUE,
  EDIT_QUEUE,
  REMOVE_QUEUE,
  GET_OWNER_QUEUES,
  GET_USER_QUEUES,
} = queueTypes;

const initialState = {
  queues: undefined,
  userQueues: undefined,
};

export const queueReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_QUEUE:
      return {
        ...state,
        queues: {
          ...state.queues,
          [action.payload.id]: action.payload,
        },
      };
    case EDIT_QUEUE:
      return {
        ...state,
      };
    case REMOVE_QUEUE:
      const queues = state.queues;
      delete queues[action.payload.id];
      console.log(queues);
      return {
        ...state,
        queues: { ...queues },
      };
    case GET_OWNER_QUEUES:
      return {
        ...state,
        queues: action.payload.queues,
      };
    case GET_USER_QUEUES:
      return {
        ...state,
        userQueues: action.payload.queues,
      };
    default:
      return state;
  }
};
