import { useEffect, useMemo } from "react";
import { Link } from "react-router-dom";
import { useParams } from "react-router-dom";

export const NoticeComponent = ({ getUserQueues, queues, isLoading }) => {
  const { id } = useParams();
  useEffect(() => {
    getUserQueues();
  }, [getUserQueues]);
  const notice = useMemo(() => {
    if (queues) {
      let notice;
      const queue = Object.values(queues).find(({ notices }) => {
        let isNoticeInCurrentQueue = Object.values(notices).find(
          (notice) => notice.id + "" === id
        );
        console.log(notices, id);
        if (isNoticeInCurrentQueue) {
          notice = isNoticeInCurrentQueue;
          return true;
        }
        return false;
      });
      if (notice) {
        return {
          ...notice,
          ahead: Object.values(queue.notices).filter(
            ({ position, status }) =>
              position < notice.position && status !== "confirmed"
          ).length,
        };
      }
    }
  }, [id, queues]);
  if (isLoading) {
    return <p>Loading...</p>;
  }

  if (!notice) {
    return <p>No existe este aviso</p>;
  }
  console.log(notice);
  return (
    <div>
      <h1>{notice.status}</h1>
      {notice.status === "called" && (
        <Link to={`/notices/${id}/confirm-notice`}>
          <button>confirmar</button>
        </Link>
      )}
    </div>
  );
};
