import {
  getOwnerQueuesApi,
  getQueueNoticesApi,
  addQueueApi,
  editQueueApi,
  removeQueueApi,
  getQrCodeQueueApi,
  getQueueApi,
  getUserNoticesApi,
} from "./api";
import { getUserStorage } from "./localStorage";

export const getOwnerQueuesService = async () => {
  const queues = await Promise.all(await getOwnerQueuesApi(getUserStorage()));
  const queuesWithNotices = await getQueuesNoticesAndFillQueues(queues);
  return queuesWithNotices.reduce((total, queue) => {
    return { ...total, [queue.id]: queue };
  }, {});
};

export const getUserQueuesService = async () => {
  const notices = await Promise.all(await getUserNoticesApi(getUserStorage()));
  const queues = await Promise.all(
    notices.map(async (notice) => await getQueueApi({ id: notice.queueId }))
  );
  const queuesWithNotices = await getQueuesNoticesAndFillQueues(queues);
  return queuesWithNotices.reduce((total, queue) => {
    return { ...total, [queue.id]: queue };
  }, {});
};

const getQueuesNoticesAndFillQueues = async (queues) =>
  await Promise.all(
    queues.map(async (queue) => ({
      ...queue,
      notices: (
        await getQueueNoticesApi(queue)
      ).reduce((total, notice) => {
        return { ...total, [notice.id]: notice };
      }, {}),
    }))
  );

export const addQueueService = async (queue) => ({
  ...(await addQueueApi({ ...queue, ownerId: getUserStorage().id })),
  notices: [],
});

export const addQrCodeToQueueService = async (queueWithQr) => ({
  ...(await editQueueApi(queueWithQr)),
});

export const getQrCodeQueueService = getQrCodeQueueApi;
export const editQueueService = editQueueApi;
export const removeQueueService = removeQueueApi;
