import React from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import {
  AddNotice,
  AddQueue,
  NotFound,
  Notice,
  Login,
  Register,
  Home,
  MyQueues,
  Queue,
  EditQueue,
  AddQrCodeToQueue,
  JoinQueue,
  MyNotices,
  ConfirmNotice,
  QueueRoom,
  SetLocalUser,
  QueueQrCode,
} from "./pages";
import { connect } from "react-redux";
import { logoutUserAction } from "./redux/actions";

export const RouterComponent = ({ user, logoutUser }) => {
  return (
    <div className="container">
      <BrowserRouter>
        <Switch>
          <Route path="/login">
            <Login />
          </Route>
          <Route path="/register">
            <Register />
          </Route>
          <Route exact path="/">
            <Home />
          </Route>
          <Route path="/add-notice">
            <AddNotice />
          </Route>
          <Route path="/add-queue">
            <AddQueue />
          </Route>
          <Route path="/notices/:id/confirm-notice">
            <ConfirmNotice />
          </Route>
          <Route path="/notices/:id">
            <Notice />
          </Route>
          <Route path="/my-queues">
            <MyQueues />
          </Route>
          <Route path="/my-notices">
            <MyNotices />
          </Route>
          <Route path="/queues/:id/qr-code">
            <QueueQrCode />
          </Route>
          <Route path="/queues/:id/edit">
            <EditQueue />
          </Route>
          <Route path="/queues/:id/add-qr-code">
            <AddQrCodeToQueue />
          </Route>
          <Route path="/queues/:id/room">
            <QueueRoom />
          </Route>
          <Route path="/queues/:id">
            <Queue />
          </Route>
          <Route path="/join-queue">
            <JoinQueue />
          </Route>
          <Route path="/set-local-user">
            <SetLocalUser />
          </Route>
          <Route path="*">
            <NotFound />
          </Route>
        </Switch>
      </BrowserRouter>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    user: state.user.user,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    logoutUser: (credentials) => {
      dispatch(logoutUserAction(credentials));
    },
  };
};

export const Router = connect(
  mapStateToProps,
  mapDispatchToProps
)(RouterComponent);
