import { QrReader } from "../../components/QrReader";
import { Redirect, useHistory } from "react-router-dom";

export const JoinQueueComponent = ({ createNoticeWithQr, user }) => {
  const history = useHistory();
  console.log(user);
  if (!user.username) {
    return <Redirect to="/set-local-user" push />;
  }
  return (
    <QrReader
      setQr={(qrCode) => {
        if (qrCode)
          createNoticeWithQr(qrCode, () => history.push(`/my-notices`));
      }}
    />
  );
};
