import { connect } from "react-redux";
import {
  getOwnerQueuesAction,
  removeQueueAction,
} from "../../redux/actions/queueActions";
import {
  callNoticeAction,
  finishNoticeAction,
} from "../../redux/actions/noticeActions";
import { QueueComponent } from "./Queue";

const mapStateToProps = (state) => {
  return {
    queues: state.queue.queues,
    isLoading: state.global.isLoading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    getOwnerQueues: () => dispatch(getOwnerQueuesAction()),
    removeQueue: (queue) => dispatch(removeQueueAction(queue)),
    callNotice: (notice) => dispatch(callNoticeAction(notice)),
    finishNotice: (notice) => dispatch(finishNoticeAction(notice)),
  };
};

export const Queue = connect(
  mapStateToProps,
  mapDispatchToProps
)(QueueComponent);
