import { QrReader } from "../../components/QrReader";
import { useHistory, useParams } from "react-router-dom";

export const ConfirmNoticeComponent = ({ confirmNoticeWithQr }) => {
  const history = useHistory();
  const { id } = useParams();

  return (
    <QrReader
      setQr={(qrCode) => {
        if (qrCode)
          confirmNoticeWithQr({ qrCode, id }, () => {
            alert("es tu turno!");
            history.push("/my-notices");
          });
      }}
    />
  );
};
