import { useEffect } from "react";
import { useParams, Link } from "react-router-dom";

export const QueueRoomComponent = ({
  getOwnerQueues,
  queues,
  isLoading,
  finishNotice,
}) => {
  useEffect(() => {
    getOwnerQueues();
  }, [getOwnerQueues]);
  const { id } = useParams();
  if (isLoading) {
    return <p>Loading...</p>;
  }
  if (!queues || !queues[id]) {
    return <p>No existe esta fila</p>;
  }

  return (
    <div>
      <h1>{queues[id].name}</h1>
      {queues[id]?.notices &&
        Object.values(queues[id].notices).map(({ client, status, id }) => {
          if (status !== "confirmed") {
            return null;
          }
          return (
            <div key={id}>
              {id} - {client.username} -{" "}
              <button
                onClick={() => {
                  finishNotice({ id });
                }}
              >
                salir
              </button>
            </div>
          );
        })}
      <Link to={`/queues/${id}`}>esperando</Link>
    </div>
  );
};
