import { useParams } from "react-router-dom";
import QRCode from "react-qr-code";

export const NoticeDetailsComponent = () => {
  const { id } = useParams();

  return (
    <div className="notice-details">
      <article>
        <QRCode value={`${process.env.REACT_APP_APP_URL}/notice/${id}`} />
      </article>
    </div>
  );
};
