import { connect } from "react-redux";
import {
  getOwnerQueuesAction,
  editQueueAction,
} from "../../redux/actions/queueActions";
import { QueueQrCodeComponent } from "./QueueQrCodeComponent";

const mapStateToProps = (state) => {
  return {
    queues: state.queue.queues,
    isLoading: state.global.isLoading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    getOwnerQueues: () => dispatch(getOwnerQueuesAction()),
    editQueue: (modifiedQueue, onSuccess) =>
      dispatch(editQueueAction(modifiedQueue, onSuccess)),
  };
};

export const QueueQrCode = connect(
  mapStateToProps,
  mapDispatchToProps
)(QueueQrCodeComponent);
