import { useHistory } from "react-router-dom";
import { useState } from "react";

export const SetLocalUserComponent = ({ setLocalUser }) => {
  const [username, setUsername] = useState("");
  const history = useHistory();

  return (
    <div>
      Como quieres que te llamen?
      <input
        value={username}
        onChange={({ target }) => {
          setUsername(target.value);
        }}
      />
      <button
        onClick={() => {
          setLocalUser(
            { username, id: new Date().getTime(), isLocal: true },
            () => {
              history.goBack();
            }
          );
        }}
      >
        Listo
      </button>
    </div>
  );
};
