import {
  getOwnerQueuesService,
  addQueueService,
  editQueueService,
  removeQueueService,
  addQrCodeToQueueService,
  getUserQueuesService,
} from "../../services";
import { queueTypes } from "../types";
import { callApiHelperAction } from "./globalAction";

const {
  ADD_QUEUE,
  EDIT_QUEUE,
  REMOVE_QUEUE,
  GET_OWNER_QUEUES,
  GET_USER_QUEUES,
} = queueTypes;

export const addQueueAction = (newQueue, onSuccess) => async (dispatch) => {
  callApiHelperAction(async () => {
    const queue = await addQueueService(newQueue);
    console.log(queue);
    dispatch({
      type: ADD_QUEUE,
      payload: queue,
    });
    if (onSuccess) {
      onSuccess();
    }
  }, dispatch);
};

export const editQueueAction =
  (modifiedQueue, onSuccess) => async (dispatch) => {
    callApiHelperAction(async () => {
      const queue = await editQueueService(modifiedQueue);
      dispatch({
        type: EDIT_QUEUE,
        payload: queue,
      });
      if (onSuccess) {
        onSuccess();
      }
    }, dispatch);
  };

export const removeQueueAction = (deletedQueue) => async (dispatch) => {
  callApiHelperAction(async () => {
    await removeQueueService(deletedQueue);
    dispatch({
      type: REMOVE_QUEUE,
      payload: deletedQueue,
    });
  }, dispatch);
};

export const getOwnerQueuesAction = () => async (dispatch) => {
  callApiHelperAction(async () => {
    const queues = await getOwnerQueuesService();
    dispatch({
      type: GET_OWNER_QUEUES,
      payload: { queues },
    });
  }, dispatch);
};

export const getUserQueuesAction = () => async (dispatch) => {
  callApiHelperAction(async () => {
    const queues = await getUserQueuesService();
    console.log(queues);
    dispatch({
      type: GET_USER_QUEUES,
      payload: { queues },
    });
  }, dispatch);
};

export const addQrCodeToQueueAction = (queueWithQr) => async (dispatch) => {
  callApiHelperAction(async () => {
    const queue = await addQrCodeToQueueService(queueWithQr);
    dispatch({
      type: EDIT_QUEUE,
      payload: queue,
    });
  }, dispatch);
};
