import { useState } from "react";
import { useHistory, Redirect } from "react-router-dom";
import { useQuery } from "../../hooks/useQuery";

export const AddNoticeComponent = ({ addNotice, user }) => {
  const [name, setName] = useState("");
  const history = useHistory();
  const query = useQuery();
  if (query.get("isOwner") !== "true") {
    if (!user.username) {
      return <Redirect to="/set-local-user" push />;
    }
    addNotice(
      {
        client: user,
        queueId: query.get("queueId"),
        status: "waiting",
      },
      () => {
        history.push("/my-notices");
      }
    );
    return <div>Loading</div>;
  }

  return (
    <div className="create">
      <label>Nombre:</label>
      <input
        type="text"
        required
        value={name}
        onChange={({ target }) => {
          setName(target.value);
        }}
      />
      <button
        className="button"
        onClick={() => {
          addNotice({
            client: { username: name },
            queueId: query.get("queueId"),
            status: "waiting",
            withoutApp: true,
          });
        }}
      >
        Agregar
      </button>
    </div>
  );
};
