import {
  addNoticeService,
  editNoticeService,
  removeNoticeService,
  createNoticeWithQrService,
  callNoticeService,
  confirmNoticeWithQrService,
  finishNoticeService,
} from "../../services";
import { callApiHelperAction } from "./globalAction";
import { noticeTypes } from "../types";

const { ADD_NOTICE } = noticeTypes;

export const addNoticeAction = (newNotice, onSuccess) => async (dispatch) => {
  callApiHelperAction(async () => {
    await addNoticeService(newNotice);
    if (onSuccess) {
      onSuccess();
    }
  }, dispatch);
};

export const editNoticeAction = (modifiedNotice) => async (dispatch) => {
  callApiHelperAction(async () => {
    await editNoticeService(modifiedNotice);
  }, dispatch);
};

export const removeNoticeAction = (deletedNotice) => async (dispatch) => {
  callApiHelperAction(async () => {
    await removeNoticeService(deletedNotice);
  }, dispatch);
};

export const createNoticeWithQrAction =
  (qrCode, onSuccess) => async (dispatch) => {
    callApiHelperAction(async () => {
      const notice = await createNoticeWithQrService(qrCode);
      dispatch({
        type: ADD_NOTICE,
        payload: notice,
      });
      if (onSuccess) {
        onSuccess();
      }
    }, dispatch);
  };

export const callNoticeAction = (notice) => async (dispatch) => {
  callApiHelperAction(async () => {
    await callNoticeService(notice);
  }, dispatch);
};

export const confirmNoticeWithQrAction =
  (notice, onSuccess) => async (dispatch) => {
    callApiHelperAction(async () => {
      await confirmNoticeWithQrService(notice);
      if (onSuccess) {
        onSuccess();
      }
    }, dispatch);
  };

export const finishNoticeAction = (notice, onSuccess) => async (dispatch) => {
  callApiHelperAction(async () => {
    await finishNoticeService(notice);
    if (onSuccess) {
      onSuccess();
    }
  }, dispatch);
};
