import { connect } from "react-redux";
import { addQrCodeToQueueAction } from "../../redux/actions/queueActions";
import { AddQrCodeToQueueComponent } from "./AddQrCodeToQueueComponent";

const mapStateToProps = (state) => {
  return {
    queues: state.queue.queues,
    isLoading: state.global.isLoading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    addQrCodeToQueue: (queueWithQr) =>
      dispatch(addQrCodeToQueueAction(queueWithQr)),
  };
};

export const AddQrCodeToQueue = connect(
  mapStateToProps,
  mapDispatchToProps
)(AddQrCodeToQueueComponent);
