import {
  registerUserService,
  loginUserService,
  logoutUserService,
  setLocalUserService,
} from "../../services";
import { userTypes } from "../types";
import { callApiHelperAction } from "./globalAction";

const { REGISTER_USER, LOGIN_USER, LOCAL_USER } = userTypes;

export const registerUserAction = (credentials) => async (dispatch) => {
  callApiHelperAction(async () => {
    const userAndToken = await registerUserService(credentials);
    dispatch({
      type: REGISTER_USER,
      payload: userAndToken,
    });
  }, dispatch);
};

export const loginUserAction = (credentials, onSuccess) => async (dispatch) => {
  callApiHelperAction(async () => {
    const userAndToken = await loginUserService(credentials);
    dispatch({
      type: LOGIN_USER,
      payload: userAndToken,
    });
    if (onSuccess) {
      onSuccess();
    }
  }, dispatch);
};

export const logoutUserAction = () => async () => {
  logoutUserService();
};

export const setLocalUserAction = (user, onSuccess) => (dispatch) => {
  setLocalUserService(user);
  dispatch({
    type: LOCAL_USER,
    payload: { user },
  });
  if (onSuccess) {
    onSuccess();
  }
};
