import {
  addNoticeApi,
  removeNoticeApi,
  editNoticeApi,
  getQrCodeQueueApi,
  getNoticeApi,
} from "./api";
import { getUserStorage } from "./localStorage";

export const addNoticeService = addNoticeApi;
export const removeNoticeService = removeNoticeApi;
export const editNoticeService = editNoticeApi;

export const createNoticeWithQrService = async (qrCode) => {
  const queue = (await getQrCodeQueueApi({ qrCode }))[0];
  if (!queue) {
    throw new Error("no se encontro la fila");
  }
  const { id, username } = getUserStorage();
  const notice = await addNoticeApi({
    client: { id, username },
    queueId: queue.id,
    status: "waiting",
  });
  return notice;
};

export const confirmNoticeWithQrService = async ({ qrCode, id }) => {
  const queue = (await getQrCodeQueueApi({ qrCode }))[0];
  if (!queue) {
    throw new Error("no se encontro la fila");
  }
  const notice = await getNoticeApi({ id });
  const user = getUserStorage();
  if (!notice || notice.client.id !== user.id || notice.status !== "called") {
    throw new Error("no es tu turno");
  }
  if (queue.hasRoom) {
    return await editNoticeApi({ id, status: "confirmed" });
  }
  await removeNoticeApi({ id });
};

export const callNoticeService = async ({ id, box }) => {
  return await editNoticeApi({ id, status: "called", box });
};

export const confirmNoticeService = async ({ id }) => {
  return await editNoticeApi({ id, status: "confirmed" });
};

export const finishNoticeService = async ({ id }) => {
  return await removeNoticeApi({ id });
};
