import { useEffect, useMemo } from "react";
import { Link, useHistory } from "react-router-dom";

export const MyNoticesComponent = ({
  isLoading,
  getUserQueues,
  queues,
  user,
  finishNotice,
}) => {
  useEffect(() => {
    getUserQueues();
  }, [getUserQueues]);
  const history = useHistory();
  const notices = useMemo(() => {
    if (queues) {
      return Object.values(queues).map(({ notices, name }) => {
        const notice = Object.values(notices).find(
          ({ client }) => client.id === user.id
        );
        return {
          ...notice,
          queueName: name,
          ahead: Object.values(notices).filter(
            ({ position, status }) =>
              position < notice.position && status !== "confirmed"
          ).length,
        };
      });
    }
    return [];
  }, [queues, user]);
  console.log(queues);
  if (isLoading) {
    return <p>Loading...</p>;
  }
  if (!queues) {
    return <div />;
  }
  if (notices.length === 0) {
    history.push("/");
  }
  return (
    <div>
      <h1 className="title" style={{ margin: "20px" }}>
        Mis turnos
      </h1>
      {notices.map((notice) => (
        <div class="card" style={{ margin: "20px" }}>
          <header class="card-header">
            <p class="card-header-title">{notice.queueName}</p>
            <button
              class="card-header-icon"
              aria-label="more options"
              onClick={() => {
                finishNotice(notice);
              }}
            >
              <span class="icon">
                <i class="fas fa-times" aria-hidden="true"></i>
              </span>
            </button>
          </header>
          <div class="card-content">
            <div class="content">
              {notice.status !== "called" &&
                (notice.ahead ? (
                  <p>Tenes a {notice.ahead} personas adelante tuyo.</p>
                ) : (
                  <p>Sos el siguiente.</p>
                ))}
              {notice.status === "called" && (
                <div>
                  <p>Ya fuiste llamado!</p>
                </div>
              )}
              <button
                className="button is-danger"
                onClick={() => {
                  finishNotice(notice);
                }}
              >
                dejar fila
              </button>
            </div>
          </div>
        </div>
      ))}
      <Link to="/join-queue">
        <button className="button">Unirme a otra fila</button>
      </Link>
    </div>
  );
};
