import { noticeTypes } from "../types";

const { ADD_NOTICE } = noticeTypes;
const initialState = {
  notices: {},
};

export const noticeReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_NOTICE:
      return {
        ...state,
        notices: {
          ...state.notices,
          [action.payload.id]: action.payload,
        },
      };
    default:
      return state;
  }
};
