import { useEffect } from "react";
import { Link } from "react-router-dom";

export const MyQueuesComponent = ({
  getOwnerQueues,
  queues,
  removeQueue,
  isLoading,
}) => {
  useEffect(() => {
    getOwnerQueues();
  }, [getOwnerQueues]);
  if (isLoading) {
    return <p>Loading...</p>;
  }
  if (!queues) {
    return <div></div>;
  }

  return (
    <div>
      <table class="table">
        <thead>
          <tr>
            <th>Nombre</th>
            <th>Acciones</th>
          </tr>
        </thead>
        <tbody>
          {Object.values(queues).map((queue) => (
            <tr key={queue.id}>
              <td>
                <Link to={`/queues/${queue.id}`}>{queue.name}</Link>
              </td>
              <td>
                <Link to={`/queues/${queue.id}/edit`}>edit</Link>
                <button
                  onClick={() => {
                    removeQueue(queue);
                  }}
                >
                  x
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
      <Link to="/add-queue">
        <button className="button">Agregar fila</button>
      </Link>
    </div>
  );
};
