import { apiInstance } from "./apiInstance";

const NOTICES = "/notices";

export const addNoticeApi = async (notice) => {
  return await apiInstance.post(`${NOTICES}`, notice);
};

export const removeNoticeApi = async ({ id }) => {
  return await apiInstance.delete(`${NOTICES}/${id}`);
};

export const editNoticeApi = async ({ id, ...notice }) => {
  return await apiInstance.patch(`${NOTICES}/${id}`, notice);
};

export const getQueueNoticesApi = async ({ id }) => {
  return await apiInstance.get(`${NOTICES}?queueId=${id}`);
};

export const getUserNoticesApi = async ({ id }) => {
  return await apiInstance.get(`${NOTICES}?client.id=${id}`);
};

export const getNoticeApi = async ({ id }) => {
  return await apiInstance.get(`${NOTICES}/${id}`);
};
