import { connect } from "react-redux";
import {
  getOwnerQueuesAction,
  removeQueueAction,
} from "../../redux/actions/queueActions";
import { finishNoticeAction } from "../../redux/actions/noticeActions";
import { QueueRoomComponent } from "./QueueRoomComponent";

const mapStateToProps = (state) => {
  return {
    queues: state.queue.queues,
    isLoading: state.global.isLoading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    getOwnerQueues: () => dispatch(getOwnerQueuesAction()),
    removeQueue: (queue) => dispatch(removeQueueAction(queue)),
    finishNotice: (notice) => dispatch(finishNoticeAction(notice)),
  };
};

export const QueueRoom = connect(
  mapStateToProps,
  mapDispatchToProps
)(QueueRoomComponent);
