import { useState } from "react";
import { Link, useHistory, useParams } from "react-router-dom";

export const AddQueueComponent = ({ addQueue }) => {
  const [newQueue, setModifiedQueue] = useState({
    name: "",
    guests: [],
    hasRoom: false,
    hasBoxes: false,
  });
  const { id } = useParams();
  const history = useHistory();

  if (!newQueue) {
    return <div />;
  }

  return (
    <div style={{ display: "flex", flexDirection: "column" }}>
      <input
        value={newQueue.name}
        onChange={({ target }) =>
          setModifiedQueue({ ...newQueue, name: target.value })
        }
      />
      <label>
        has room
        <input
          type="checkbox"
          checked={!!newQueue.hasRoom}
          onChange={() =>
            setModifiedQueue({
              ...newQueue,
              hasRoom: !newQueue.hasRoom,
            })
          }
          style={{
            backgroundColor: "initial",
            cursor: "default",
            appearance: "auto",
            boxSizing: "border-box",
            margin: "3px 3px 3px 4px",
            padding: "initial",
            border: "initial",
          }}
        />
      </label>
      <label>
        has boxes
        <input
          type="checkbox"
          checked={!!newQueue.hasBoxes}
          onChange={() =>
            setModifiedQueue({
              ...newQueue,
              hasBoxes: !newQueue.hasBoxes,
            })
          }
          style={{
            backgroundColor: "initial",
            cursor: "default",
            appearance: "auto",
            boxSizing: "border-box",
            margin: "3px 3px 3px 4px",
            padding: "initial",
            border: "initial",
          }}
        />
      </label>
      <Link to={`/queues/${id}/add-qr-code`}>set qr code</Link>
      <button
        onClick={() => {
          addQueue(newQueue, () => {
            history.goBack();
          });
        }}
      >
        Guardar
      </button>
    </div>
  );
};
