import { connect } from "react-redux";
import { getUserQueuesAction } from "../../redux/actions/queueActions";
import { finishNoticeAction } from "../../redux/actions/noticeActions";
import { MyNoticesComponent } from "./MyNoticesComponent";

const mapStateToProps = (state) => {
  return {
    queues: state.queue.userQueues,
    isLoading: state.global.isLoading,
    user: state.user.user,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    getUserQueues: () => dispatch(getUserQueuesAction()),
    finishNotice: (notice, onSuccess) =>
      dispatch(finishNoticeAction(notice, onSuccess)),
  };
};

export const MyNotices = connect(
  mapStateToProps,
  mapDispatchToProps
)(MyNoticesComponent);
