import { QrReader as Reader } from "react-qr-reader";

export const QrReader = ({ setQr }) => (
  <Reader
    constraints={{ facingMode: "environment" }}
    onResult={(result) => {
      if (!!result) {
        setQr(result?.text);
      }
    }}
    containerStyle={{
      height: "100vh",
    }}
    videoContainerStyle={{
      height: "100%",
      paddingTop: 0,
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
    }}
    videoStyle={{
      height: "100%",
      width: "auto",
    }}
  />
);
