import { getStoredUserAndTokenService } from "../../services";
import { userTypes } from "../types";

const { REGISTER_USER, LOGIN_USER, LOCAL_USER } = userTypes;
const initialState = {
  ...getStoredUserAndTokenService(),
};

export const userReducer = (state = initialState, action) => {
  switch (action.type) {
    case REGISTER_USER:
    case LOGIN_USER:
      return {
        ...state,
        user: action.payload.user,
        token: action.payload.accessToken,
      };
    case LOCAL_USER:
      return {
        ...state,
        user: action.payload.user,
      };
    default:
      return state;
  }
};
