import { connect } from "react-redux";
import { confirmNoticeWithQrAction } from "../../redux/actions/noticeActions";
import { ConfirmNoticeComponent } from "./ConfirmNoticeComponent";

const mapStateToProps = (state) => {
  return {
    queues: state.queue.queues,
    isLoading: state.global.isLoading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    confirmNoticeWithQr: (notice, onSuccess) =>
      dispatch(confirmNoticeWithQrAction(notice, onSuccess)),
  };
};

export const ConfirmNotice = connect(
  mapStateToProps,
  mapDispatchToProps
)(ConfirmNoticeComponent);
