import { registerUserApi, loginUserApi } from "./api";
import {
  saveUserAndTokenStorage,
  getTokenStorage,
  getUserStorage,
  saveUserStorage,
} from "./localStorage";

export const registerUserService = async (credentials) => {
  const userAndToken = await registerUserApi(credentials);
  saveUserAndTokenStorage(userAndToken);
  return userAndToken;
};
export const loginUserService = async (credentials) => {
  const userAndToken = await loginUserApi(credentials);
  saveUserAndTokenStorage(userAndToken);
  return userAndToken;
};

export const logoutUserService = () => {
  saveUserAndTokenStorage({ user: {}, accessToken: "" });
  window.location.replace("/");
};

export const getStoredUserAndTokenService = () => ({
  user: getUserStorage() || {},
  token: getTokenStorage() || "",
});

export const setLocalUserService = (user) => {
  saveUserStorage(user);
};
