import { connect } from "react-redux";
import {
  getOwnerQueuesAction,
  removeQueueAction,
} from "../../redux/actions/queueActions";
import { MyQueuesComponent } from "./MyQueues";

const mapStateToProps = (state) => {
  return {
    queues: state.queue.queues,
    isLoading: state.global.isLoading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    getOwnerQueues: () => dispatch(getOwnerQueuesAction()),
    removeQueue: (queue) => dispatch(removeQueueAction(queue)),
  };
};

export const MyQueues = connect(
  mapStateToProps,
  mapDispatchToProps
)(MyQueuesComponent);
