import { connect } from "react-redux";
import { SetLocalUserComponent } from "./SetLocalUserComponent";
import { setLocalUserAction } from "../../redux/actions/userActions";

const mapStateToProps = (state) => {
  return {
    user: state.user.user,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    setLocalUser: (user, onSuccess) =>
      dispatch(setLocalUserAction(user, onSuccess)),
  };
};

export const SetLocalUser = connect(
  mapStateToProps,
  mapDispatchToProps
)(SetLocalUserComponent);
