import { globalTypes } from "../types";

const { SET_ERROR_MESSAGE, SET_IS_LOADING, UNSET_IS_LOADING } = globalTypes;

export const callApiHelperAction = async (promise, dispatch) => {
  try {
    dispatch({
      type: SET_IS_LOADING,
    });
    await promise();
  } catch (error) {
    alert(error);
    dispatch({
      type: SET_ERROR_MESSAGE,
      payload: error,
    });
  }
  dispatch({
    type: UNSET_IS_LOADING,
  });
};
