import { connect } from "react-redux";
import { AddQueueComponent } from "./AddQueueComponent";
import { addQueueAction } from "../../redux/actions";

const mapStateToProps = (state) => {
  return {};
};

const mapDispatchToProps = (dispatch) => {
  return {
    addQueue: (newQueue, onSuccess) =>
      dispatch(addQueueAction(newQueue, onSuccess)),
  };
};

export const AddQueue = connect(
  mapStateToProps,
  mapDispatchToProps
)(AddQueueComponent);
